  margin-right: auto;
  background-color: rgba(0, 0, 0, 0);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(6px);
